.input {
  color: rgb(0, 0, 0);
  border: none;
  padding: 0;
  border-radius: 0;
  outline: none;
  width: auto;
  min-width: 100%;
  font-size: 16px;
  line-height: 2em;
  font-family: Arial, sans-serif;
  border-bottom: 3px solid #000000;
  background-color: transparent;
}

.submit {
  margin-top: 10px;
  cursor: pointer;
  background: rgb(253, 253, 253);
  border: 1px solid rgb(0, 0, 0);
}
