.button {
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  background: rgb(253, 253, 253);
  border: 1px solid rgb(0, 0, 0);
  cursor: pointer;
}

.img {
  float: left;
  padding: 0 15px;
}

h2 {
  color: #000000;
  font-size: 24px;
  padding: 0;
}

h3 {
  color: #000000;
  font-size: 20px;
}

.list {
  line-height: 2rem;
  list-style-type: none;
  text-align: left;
  display: flex;
  padding: 0;
}

.list a {
  color: #000000;
  text-decoration: none;
  font-size: 14px;
  margin-right: 25px;
  border: 1px solid rgb(0, 0, 0);
  padding: 2px 3px;
}
